import React from 'react';
import mealBook from './data2.json';
import MealCard from "./mealCard";

export default function MealData({search, listAllergy, setCount, count}) {
    let check = -1;
    let safe = -1;

    if(count >= mealBook.length){
        setCount(0)
    }


    let chunk = mealBook.slice(count, count + 1000);

    return(
        <>
            <div>
                { chunk.map((meal)=>{

                    // let check2 = listAllergy.find(x => x.name === "Dairy")
                    // console.log(check2)
                    meal.allergens.map((avoid) =>{
                        let check2 = listAllergy.find(x => x.name === avoid);
                        if(check2) {
                            if (check2.selected === true) {
                                safe = 1
                            }

                        }
                    })
                    if( safe === -1) {
                        if (meal.name.search(search) !== -1) {
                            return <MealCard meal = {meal}/>
                        } else {
                            meal.ingredients.map((goods) => {
                                if (goods.search(search) === 0) {
                                    check = 1;
                                }
                            })
                            if (check === 1) {
                                check = -1;
                                return <MealCard meal = {meal}/>

                            }

                        }
                    }
                    else{
                        safe = -1;
                    }
                })
                }
            </div>
        </>
    )
}
